import React from "react";
import Layout from "./layout";
import Tag from "./tag";

const kebabCase = require("lodash/kebabCase");

export default ({children, pageContext}) => {

  const date = new Date(pageContext.frontmatter.date).toLocaleDateString();

  return (
    <Layout id="mdx">
      <h1>{pageContext.frontmatter.title}</h1>
      <span>{pageContext.frontmatter.author}</span>

      {pageContext.frontmatter.tags &&
       pageContext.frontmatter.tags.map(tag => (
         <Tag key={kebabCase(tag)} to={`/tags/${kebabCase(tag)}/`}>
           {tag}
         </Tag>))}

      <div className="right">at <time>{date}</time></div>
      <hr/>
      {children}
    </Layout>
  );
}
