import React from "react";
import {Link} from "gatsby";

const NavLink = ({to, children}) => (
  <Link className="no-underline text-teal-dark uppercase tracking-wide font-bold text-xl sm:mx-4 mx-auto" to={to}>
    {children}
  </Link>
);

export default () => (
  <header className="main-container mx-auto my-5">
    <nav className="flex justify-end">
      <NavLink to="/">Blogs</NavLink>
      <NavLink to="/tags">Tags</NavLink>
      <NavLink to="/about">About</NavLink>
    </nav>
  </header>
)
